___CSS_LOADER_EXPORT___.push([module.id, "\n#app-content > div[data-v-067c0852] {\n\t\twidth: 100%;\n\t\theight: 100%;\n\t\tpadding: 20px;\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\tflex-grow: 1;\n}\ninput[type='text'][data-v-067c0852] {\n\t\twidth: 100%;\n}\ntextarea[data-v-067c0852] {\n\t\tflex-grow: 1;\n\t\twidth: 100%;\n}\n.main-wrapper[data-v-067c0852] {\n    width: 100%;\n    padding: 1rem;\n}\n\n  /* AppControls */\n.controls-wrapper[data-v-067c0852] {\n    --nc-button-size: 44px;\n    --vertical-padding: 4px;\n    position: sticky;\n    z-index: 2;\n    top: 0;\n    display: flex;\n    width: 100%;\n    min-height: calc(44px + 2 * var(--vertical-padding));\n    flex-direction: row;\n    padding: var(--vertical-padding) 1rem var(--vertical-padding) calc(44px + 2 * var(--vertical-padding));\n    border-bottom: 1px solid var(--color-border);\n    background-color: var(--color-main-background);\n    gap: 8px;\n}\n.location-wrapper[data-v-067c0852] {\n     display: flex;\n     flex: 1;\n     flex-direction: column;\n     justify-content: center;\n}\n.location[data-v-067c0852] {\n    width: 100%;\n    margin: 0;\n    font-size: 1.2em;\n    line-height: 1em;\n    overflow-x: clip;\n    overflow-y: visible;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n}\n", "",{"version":3,"sources":["webpack://./src/App.vue"],"names":[],"mappings":";AA0OA;EACA,WAAA;EACA,YAAA;EACA,aAAA;EACA,aAAA;EACA,sBAAA;EACA,YAAA;AACA;AAEA;EACA,WAAA;AACA;AAEA;EACA,YAAA;EACA,WAAA;AACA;AAEA;IACA,WAAA;IACA,aAAA;AACA;;EAEA,gBAAA;AAEA;IACA,sBAAA;IACA,uBAAA;IACA,gBAAA;IACA,UAAA;IACA,MAAA;IACA,aAAA;IACA,WAAA;IACA,oDAAA;IACA,mBAAA;IACA,sGAAA;IACA,4CAAA;IACA,8CAAA;IACA,QAAA;AACA;AAEA;KACA,aAAA;KACA,OAAA;KACA,sBAAA;KACA,uBAAA;AACA;AAEA;IACA,WAAA;IACA,SAAA;IACA,gBAAA;IACA,gBAAA;IACA,gBAAA;IACA,mBAAA;IACA,uBAAA;IACA,mBAAA;AACA","sourcesContent":["<template>\n    <!--\n    SPDX-FileCopyrightText: Frank Rohlfing <mail@frank-rohlfing.de>\n    SPDX-License-Identifier: AGPL-3.0-or-later\n    -->\n\t<NcContent app-name=\"guitarsongbook\">\n    <NcAppNavigation>\n      <NcAppNavigationNew v-if=\"!loading\"\n                          :text=\"t('guitarsongbook', 'New note')\"\n                          :disabled=\"false\"\n                          button-id=\"new-guitarsongbook-button\"\n                          button-class=\"icon-add\"\n                          @click=\"newNote\" />\n      <ul>\n        <NcAppNavigationItem v-for=\"note in notes\"\n                             :key=\"note.id\"\n                             :title=\"note.title ? note.title : t('guitarsongbook', 'New note')\"\n                             :class=\"{active: currentNoteId === note.id}\"\n                             @click=\"openNote(note)\">\n          <template slot=\"actions\">\n            <NcActionButton v-if=\"note.id === -1\"\n                            icon=\"icon-close\"\n                            @click=\"cancelNewNote(note)\">\n              {{ t('guitarsongbook', 'Cancel note creation') }}\n            </NcActionButton>\n            <NcActionButton v-else\n                            icon=\"icon-delete\"\n                            @click=\"deleteNote(note)\">\n              {{ t('guitarsongbook', 'Delete note') }}\n            </NcActionButton>\n          </template>\n        </NcAppNavigationItem>\n      </ul>\n    </NcAppNavigation>\n    <NcAppContent>\n      <div class=\"controls-wrapper\">\n        <div data-v-e345c782=\"\" class=\"location-wrapper\">\n          <h2 class=\"location\">Neuen Song anlegen</h2>\n        </div>\n      </div>\n      <!--<my-todo-item></my-todo-item>-->\n      <div class=\"main-wrapper\">\n        <div v-if=\"currentNote\">\n          <input ref=\"title\"\n            v-model=\"currentNote.title\"\n            type=\"text\"\n            :disabled=\"updating\">\n          <textarea ref=\"content\" v-model=\"currentNote.content\" :disabled=\"updating\" />\n          <input type=\"button\"\n            class=\"primary\"\n            :value=\"t('guitarsongbook', 'Save')\"\n            :disabled=\"updating || !savePossible\"\n            @click=\"saveNote\">\n        </div>\n        <div v-else id=\"emptycontent\">\n          <div class=\"icon-file\" />\n          <h2>{{\n           t('guitarsongbook', 'Create a note to get started') }}</h2>\n        </div>\n      </div>\n    </NcAppContent>\n  </NcContent>\n</template>\n\n<script>\n\nimport NcContent from '@nextcloud/vue/dist/Components/NcContent'\nimport NcAppNavigation from '@nextcloud/vue/dist/Components/NcAppNavigation'\nimport NcAppNavigationItem from '@nextcloud/vue/dist/Components/NcAppNavigationItem'\nimport NcAppNavigationNew from '@nextcloud/vue/dist/Components/NcAppNavigationNew'\nimport NcAppContent from '@nextcloud/vue/dist/Components/NcAppContent'\nimport NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton'\nimport MyTodoItem from './components/MyTodoItem'\n\nimport '@nextcloud/dialogs/styles/toast.scss'\nimport { generateUrl } from '@nextcloud/router'\nimport { showError, showSuccess } from '@nextcloud/dialogs'\nimport axios from '@nextcloud/axios'\n\nexport default {\n\tname: 'App',\n\tcomponents: {\n    NcContent,\n    NcAppNavigation,\n    NcAppNavigationItem,\n    NcAppNavigationNew,\n    NcAppContent,\n    NcActionButton,\n    MyTodoItem,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tnotes: [],\n\t\t\tcurrentNoteId: null,\n\t\t\tupdating: false,\n\t\t\tloading: true,\n\t\t}\n\t},\n\tcomputed: {\n\t\t/**\n\t\t * Return the currently selected note object\n\t\t * @returns {Object|null}\n\t\t */\n\t\tcurrentNote() {\n\t\t\tif (this.currentNoteId === null) {\n\t\t\t\treturn null\n\t\t\t}\n\t\t\treturn this.notes.find((note) => note.id === this.currentNoteId)\n\t\t},\n\n\t\t/**\n\t\t * Returns true if a note is selected and its title is not empty\n\t\t * @returns {Boolean}\n\t\t */\n\t\tsavePossible() {\n\t\t\treturn this.currentNote && this.currentNote.title !== ''\n\t\t},\n\t},\n\t/**\n\t * Fetch list of notes when the component is loaded\n\t */\n\tasync mounted() {\n\t\ttry {\n\t\t\tconst response = await axios.get(generateUrl('/apps/guitarsongbook/notes'))\n\t\t\tthis.notes = response.data\n\t\t} catch (e) {\n\t\t\tconsole.error(e)\n\t\t\tshowError(t('notestutorial', 'Could not fetch notes'))\n\t\t}\n\t\tthis.loading = false\n\t},\n\n\tmethods: {\n\t\t/**\n\t\t * Create a new note and focus the note content field automatically\n\t\t * @param {Object} note Note object\n\t\t */\n\t\topenNote(note) {\n\t\t\tif (this.updating) {\n\t\t\t\treturn\n\t\t\t}\n\t\t\tthis.currentNoteId = note.id\n\t\t\tthis.$nextTick(() => {\n\t\t\t\tthis.$refs.content.focus()\n\t\t\t})\n\t\t},\n\t\t/**\n\t\t * Action tiggered when clicking the save button\n\t\t * create a new note or save\n\t\t */\n\t\tsaveNote() {\n\t\t\tif (this.currentNoteId === -1) {\n\t\t\t\tthis.createNote(this.currentNote)\n\t\t\t} else {\n\t\t\t\tthis.updateNote(this.currentNote)\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t * Create a new note and focus the note content field automatically\n\t\t * The note is not yet saved, therefore an id of -1 is used until it\n\t\t * has been persisted in the backend\n\t\t */\n\t\tnewNote() {\n\t\t\tif (this.currentNoteId !== -1) {\n\t\t\t\tthis.currentNoteId = -1\n\t\t\t\tthis.notes.push({\n\t\t\t\t\tid: -1,\n\t\t\t\t\ttitle: '',\n\t\t\t\t\tcontent: '',\n\t\t\t\t})\n\t\t\t\tthis.$nextTick(() => {\n\t\t\t\t\tthis.$refs.title.focus()\n\t\t\t\t})\n\t\t\t}\n\t\t},\n\t\t/**\n\t\t * Abort creating a new note\n\t\t */\n\t\tcancelNewNote() {\n\t\t\tthis.notes.splice(this.notes.findIndex((note) => note.id === -1), 1)\n\t\t\tthis.currentNoteId = null\n\t\t},\n\t\t/**\n\t\t * Create a new note by sending the information to the server\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync createNote(note) {\n\t\t\tthis.updating = true\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(generateUrl('/apps/guitarsongbook/notes'), note)\n\t\t\t\tconst index = this.notes.findIndex((match) => match.id === this.currentNoteId)\n\t\t\t\tthis.$set(this.notes, index, response.data)\n\t\t\t\tthis.currentNoteId = response.data.id\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('notestutorial', 'Could not create the note'))\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\t\t/**\n\t\t * Update an existing note on the server\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync updateNote(note) {\n\t\t\tthis.updating = true\n\t\t\ttry {\n\t\t\t\tawait axios.put(generateUrl(`/apps/guitarsongbook/notes/${note.id}`), note)\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('notestutorial', 'Could not update the note'))\n\t\t\t}\n\t\t\tthis.updating = false\n\t\t},\n\t\t/**\n\t\t * Delete a note, remove it from the frontend and show a hint\n\t\t * @param {Object} note Note object\n\t\t */\n\t\tasync deleteNote(note) {\n\t\t\ttry {\n\t\t\t\tawait axios.delete(generateUrl(`/apps/guitarsongbook/notes/${note.id}`))\n\t\t\t\tthis.notes.splice(this.notes.indexOf(note), 1)\n\t\t\t\tif (this.currentNoteId === note.id) {\n\t\t\t\t\tthis.currentNoteId = null\n\t\t\t\t}\n\t\t\t\tshowSuccess(t('guitarsongbook', 'Note deleted'))\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e)\n\t\t\t\tshowError(t('guitarsongbook', 'Could not delete the note'))\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\t#app-content > div {\n\t\twidth: 100%;\n\t\theight: 100%;\n\t\tpadding: 20px;\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\tflex-grow: 1;\n\t}\n\n\tinput[type='text'] {\n\t\twidth: 100%;\n\t}\n\n\ttextarea {\n\t\tflex-grow: 1;\n\t\twidth: 100%;\n\t}\n\n  .main-wrapper {\n    width: 100%;\n    padding: 1rem;\n  }\n\n  /* AppControls */\n\n  .controls-wrapper {\n    --nc-button-size: 44px;\n    --vertical-padding: 4px;\n    position: sticky;\n    z-index: 2;\n    top: 0;\n    display: flex;\n    width: 100%;\n    min-height: calc(44px + 2 * var(--vertical-padding));\n    flex-direction: row;\n    padding: var(--vertical-padding) 1rem var(--vertical-padding) calc(44px + 2 * var(--vertical-padding));\n    border-bottom: 1px solid var(--color-border);\n    background-color: var(--color-main-background);\n    gap: 8px;\n  }\n\n  .location-wrapper {\n     display: flex;\n     flex: 1;\n     flex-direction: column;\n     justify-content: center;\n   }\n\n  .location {\n    width: 100%;\n    margin: 0;\n    font-size: 1.2em;\n    line-height: 1em;\n    overflow-x: clip;\n    overflow-y: visible;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n  }\n</style>\n"],"sourceRoot":""}]);
